import Vue from "vue";
import layer from "@/stores/modules/layer.js";
import compose from "@/stores/modules/compose.js";
import snapshot from "@/stores/modules/snapshot.js";

export const state = {
  ...compose.state,
  ...snapshot.state,
  //  edit preview
  editMode: "edit",
  pageConfig: {
    // 
    pageSize: "A4",
    //  h  v 
    pageDirection: "h",
    // /
    title: "",
    // 100%
    scale: 1,
    // 
    background: "#ffffff",
    // 
    color: "#212121",
    // 
    fontSize: 12,
    // 
    fontFamily: "Microsoft YaHei",
  },
  // 
  isInEditor: false,
  componentData: [],
  curComponent: null,
  curComponentIndex: null,
  // 
  // 
  isClickComponent: false,
};
export const getters = {};
export const mutations = {
  ...layer.mutations,
  ...compose.mutations,
  ...snapshot.mutations,
  setClickComponentStatus(state, status) {
    state.isClickComponent = status;
  },

  setEditMode(state, mode) {
    state.editMode = mode;
  },

  setInEditorStatus(state, status) {
    state.isInEdiotr = status;
  },

  setCanvasStyle(state, style) {
    state.canvasStyleData = style;
  },

  setCurComponent(state, { component, index }) {
    state.curComponent = component;
    state.curComponentIndex = index;
  },

  setShapeStyle({ curComponent }, { top, left, width, height, rotate }) {
    if (top) curComponent.style.top = Math.round(top);
    if (left) curComponent.style.left = Math.round(left);
    if (width) curComponent.style.width = Math.round(width);
    if (height) curComponent.style.height = Math.round(height);
    if (rotate) curComponent.style.rotate = Math.round(rotate);
  },

  setShapeSingleStyle({ curComponent }, { key, value }) {
    curComponent.style[key] = value;
  },

  setComponentData(state, componentData = []) {
    Vue.set(state, "componentData", componentData);
  },

  addComponent(state, { component, index }) {
    if (index !== undefined) {
      state.componentData.splice(index, 0, component);
    } else {
      state.componentData.push(component);
    }
  },

  deleteComponent(state, index) {
    if (index === undefined) {
      index = state.curComponentIndex;
    }

    if (index === state.curComponentIndex) {
      state.curComponentIndex = null;
      state.curComponent = null;
    }

    if (/\d/.test(index)) {
      state.componentData.splice(index, 1);
    }
  },
};
export const actions = {};
